<script>
	import {onMount} from "svelte";
	import { fade } from 'svelte/transition';

	let canvas, ctx, i, prevX, prevY;
	let innerWidth;
	let innerHeight; 
	let current;
	let mounted = false;
	let strokes;
	let brushRadiusX;
	let brushRadiusY;

	let update = () => {
		current.forEach(brush => {
			for(i = 0; i < strokes; i++) {
				ctx.lineWidth = Math.random() * 70 + 10;
				ctx.beginPath();
				prevX = brush.x;
				prevY = brush.y;
				brush.x = brush.x + (Math.random() * 30 - 15);
				brush.y = brush.y + (Math.random() * 30 - 15);
				if (brush.x < 0) {
					brush.x = innerWidth;
					prevX = brush.x;
				} else if (brush.x > innerWidth) {
					brush.x = 0;
					prevX = brush.x;
				} 
				if (brush.y < 0) {
					brush.y = innerHeight;
					prevY = brush.y;
				} else if (brush.y > innerHeight) {
					brush.y = 0;
					prevY = brush.y;
				}
				ctx.moveTo(prevX, prevY);
				ctx.fillStyle = `rgba(${~~((brush.x / innerWidth) * 255)}, ${~~((brush.y / innerHeight) * 255)}, 150, 0.2)`;
				ctx.ellipse(brush.x, brush.y, brushRadiusX, brushRadiusY, (Math.random() * 6 / Math.PI), 0, 2 * Math.PI);
				ctx.fill();
				ctx.closePath();
			}
		})
	}

	onMount(() => {
		mounted = true;
	});

	$: if(innerWidth && mounted) {
		strokes = 10;
		canvas.width = innerWidth;
		canvas.height = innerHeight;
		brushRadiusX = innerWidth / 25;
		brushRadiusY = innerHeight / 25;
		current = [{
			x: innerWidth / 2,
			y: innerHeight / 2
		},
		{
			x: innerWidth / 2,
			y: innerHeight / 2
		},
		{
			x: innerWidth / 2,
			y: innerHeight / 2
		},
		{
			x: innerWidth / 2,
			y: innerHeight / 2
		},
		{
			x: innerWidth / 2,
			y: innerHeight / 2
		}];
		ctx = canvas.getContext("2d");
	}
</script>

<style>
	p {
		text-align: center;
		margin: 1em auto;
	}
	a {
		font-size: 3em;
		padding: 30px;
	}
	a::selection {
		background: #990099;
		color: #bb0099;
	}
	a:hover {
		color: #555;
	}
	canvas {
		position: fixed;
		overflow: hidden;
		top: 0;
		left: 0;
		z-index: -1;
	}
</style>

<svelte:window bind:innerWidth bind:innerHeight />
<svelte:body on:mousemove={update} on:touchstart={update} on:touchmove={update} />

<svelte:head>
	<title>Oskar Eriksson | Interactive Developer & Technical Director</title>
</svelte:head>
        
<canvas bind:this={canvas}></canvas>
        
